export default {
    name: 'order-statis-pie',
    components: {

    },
    data() {
        return {
            echarts_data: {
                id: 'orderPieDefault',
                title: '',
                subtext: '',
                hover_title: '',
                data_list: [{
                    name: '',
                    value: '0'
                }, {
                    name: '',
                    value: '0'
                }, {
                    name: '',
                    value: '0'
                }, {
                    name: '',
                    value: '0'
                }, {
                    name: '',
                    value: '0'
                }, {
                    name: '',
                    value: '0'
                }]
            }
        }
    },
    methods: {
        onUpdateTitle() {
            this.echarts_data.title = '';
        },
        onUpdateSubtext() {
            this.echarts_data.subtext = '';
        },
        onUpdateHoverTitle() {
            this.echarts_data.hover_title = '';
        },
        onUpdateValueList() {
            this.getStatis();
        },
        onUpdateTextList() {
            for (var i = 0; i < this.echarts_data.data_list.length; i++) {
                var value = this.echarts_data.data_list[i];
                value.name += '!';

                this.$set(this.echarts_data.data_list, i, value);
            }
        },


        getStatis() {
			this.$$api_order_statisOrder({
			    data:{},
                fn:data=>{
					for (var f in data.statis) {
						var value = this.echarts_data.data_list[f - 1];
						value.value = data.statis[f];
						this.$set(this.echarts_data.data_list, f - 1, value);
					}
                }
            });
        }
    },
    mounted: function() {

    }
}


