export default {
    name: 'article-list',
    components: {
        // ListData
    },
    data() {
        return {
            types:{
                'A':'',
                'B':'',
                'C':'',
                'D':''
            },
            list: [{
                id:1,
                name:'',
                type:'A',
                status:1
            },{
                id:2,
                name:'',
                type:'B',
                status:2
            },{
                id:3,
                name:'',
                type:'C',
                status:1
            },{
				id:3,
				name:'',
				type:'D',
				status:1
			}],
            fields: [{
                key: 'name',
                label: ''
            }, {
                key: 'type',
                label: '()',
				width:'260',
                filter_list: [{
                    text: 'A',
                    value: 'A'
                }, {
                    text: 'B',
                    value: 'B'
                }, {
                    text: 'C',
                    value: 'C'
                }, {
                    text: 'D',
                    value: 'D'
                }],
                filter_method: (value, item)=>{
                    return item.type == value;
                },
                filter_multiple: true,
            },  {
                key: 'status',
                label: '()',
				width:'260',
                filter_list: [{
                    text: '1',
                    value: 1
                }, {
                    text: '2',
                    value: 2
                }],
                filter_method: function(value, item) {
                    return item.status == value;
                },
                filter_multiple: false,
            }]
        }
    },
    methods: {
        onClickBtn(opts){
            console.log(opts);
        },


        onClickBtnAdd(){
            this.$message('');
        },
		onClickBtnUpdate(){
			this.$message('');
		},
		onClickBtnSelect(){
			this.$message('');
		},
		onClickBtnDelete(){
			this.$message('');
		},
    },
    mounted() {

    },
    watch:{
		$route(to, from) {

		}
    }

}



