export default {
	components: {
		// FormData
	},
	data() {
		return {
			fields: [{
				label         : '',
				type          : 'cascader',
				key           : 'cascader',
				desc          : '',
				options       : [{
					id      : 1,
					city    : '',
					children: [{
						city    : '',
						id      : 11,
						children: [{
							city    : '6',
							id      : 111,
							children: [{
								city: '',
								id  : 1111,
							}, {
								city: '',
								id  : 1112
							}, {
								city: '',
								id  : 1113
							}]
						}]
					}, {
						city: '',
						id  : 12
					}]
				}, {
					id      : 2,
					city    : '',
					children: [{
						city: '',
						id  : 21
					}]
				}],
				cascader_attrs: {
					props: {
						label   : 'city',
						value   : 'id',
						children: 'children'
					}
				},
				events        : {
					change              : ({value, info}) => {
						this.$message('' + value + '' + info);
					},
					'active-item-change': ({value, info}) => {
						this.$message('' + value + '' + info);
					}
				}

			}]
		}
	},
	methods   : {
		onSubmit(data) {
			this.$emit('onTest', data);
		}
	}
}


