export default {
    name: 'components-list-data-expand',
    components: {

    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'name',
                label: ''
            }],
            expand:{
                show:true,
                // position:'right'
            }
        }
    },
    created(){
        for(let i=1;i<=3;i++){
            this.list.push({
                name:''+i,
                address:' '+i+" ",
            });
        }
    },
    methods: {
        init() {

        }
    },
    mounted() {
        this.init();
    },
    watch:{
        $route(){

        }
    }
}


