var env = process.env;

var gbs={
	// host: '//slsadmin.api.' + (env.NODE_ENV === 'development' ? 'sls' : 'sailengsi') + '.com',
	host: '/webapi', //slsadmin.api.sls.com,Nginx
	db_prefix: 'webapi_', //key

	//
	api_status_key_field:'status',
	//value
	api_status_value_field:200,

	api_data_field:'data',

	api_custom:{
		404:function (res) {
			this.$store.dispatch('remove_userinfo').then(() => {
				this.$alert(res.status + ',' + res.msg + '', '', {
					confirmButtonText: '',
					callback: action => {
						this.$router.push('/login');
					}
				});
			});
		}
	}
};

var cbs={
	/**
	 * ajax200
	 * @param  {object} err 
	 */
	statusError(err) {
		console.log('err');
		if (err.status !== 404) {
			this.$message({
				showClose: true,
				message: '' + err.msg,
				type: 'error'
			});
		} else {
			this.$store.dispatch('remove_userinfo').then(() => {
				this.$alert(err.status + ',' + err.msg + '', '', {
					confirmButtonText: '',
					callback: action => {
						this.$router.push('/login');
					}
				});
			});
		}
	},

	/**
	 * ajax
	 */
	requestError(err) {
		this.$message({
			showClose: true,
			message: '' + (err.response ? err.response.status : '') + ',' + (err.response ? err.response.statusText : ''),
			type: 'error'
		});
	}
};

export {
	gbs,
	cbs
};


