export default {
    components: {
        // FormData
    },
    data() {
        return {
            default_value:{
                love:['chi','wan'],
				like:['du']
            },
            fields: [{
                label: '',
                type: 'checkbox',
                key: 'love',
                list:[{
                    text:'',
                    value:'chi'
                },{
					text:'',
					value:'he'
				},{
					text:'',
					value:'wan'
				}],
				events:{
                	change:({info,value})=>{
                		this.$message(''+value.join(',')+''+info.join(','));
					}
				}
            },{
				label: '',
				type: 'checkbox',
				key: 'like',
				list:[{
					text:'',
					value:'piao'
				},{
					text:'',
					value:'du'
				},{
					text:'',
					value:'shua'
				}]
			}],

        }
    },
    methods: {
        onSubmit(data) {
            this.$emit('onTest', data);
        }
    }
}


