import SlsTableHead from './SlsTableHead.vue';
export default {
	name      : 'list-data',
	components: {SlsTableHead},
	data() {
		return {
			batch_flag : true, //true,false
			batch_datas: [],
			batch_ids  : [],

			batch: {
				flag : true,
				datas: [],
				ids  : []
			},

			list    : this.List, //
			fields  : this.FieldList, //
			expand  : this.Expand,//
			btn_info: this.BtnInfo,//

			pagination: this.Pagination,//

			search: this.Search//
		}
	},
	methods   : {
		/**
		 * CheckBox
		 * @param  {array} val 
		 */
		onSelectionChange(val) {
			this.batch.datas = val;

			this.batch.ids = [];
			if (val.length) {
				this.batch.flag = false;
				for (var i = 0; i < val.length; i++) {
					this.batch.ids.push(val[i].id);
				}
			} else {
				this.batch.flag = true;
			}

			/**
			 * CheckBoxID
			 */
			this.$emit('onSelectionChange', {
				ids  : this.batch.ids,
				datas: this.batch.datas
			});
			this.$emit('onSelectionChangeObj', {
				ids  : this.batch.ids,
				datas: this.batch.datas
			});
		},

		/**
		 * 
		 * @param data    
		 */
		onSearch(opts){
			this.$emit('onSearch', opts);
		},


		/**
		 * 
		 * @param  {object || boolean} user  ,
		 * @param  {number} index 
		 */
		onBatchDelete() {
			this.$emit('onClickBtnBatchDelete', {
				ids  : this.batch.ids,
				datas: this.batch.datas
			});
		},


		/**
		 * 
		 * @param opts  
		 * @param.attr  opts.type   string      ()
		 * @param.attr  opts.index  number      
		 * @param.attr  opts.data   object      
		 * @param.attr  opts.list   array       
		 */
		onBtnEvent(opts){
			switch (opts.type) {
				case 'Add':
					this.$emit('onClickBtnAdd', opts);
					break;
				case 'Update':
					this.$emit('onClickBtnUpdate', opts);
					break;
				case 'Delete':
					this.$emit('onClickBtnDelete', opts);
					break;
				case 'BatchDelete':
					this.onBatchDelete();
					break;
				case 'Select':
					this.$emit('onClickBtnSelect', opts);
					break;
				default:
					this.$emit('onClickBtn', opts);
			}
		},


		/**
		 * 
		 * @param opts
		 */
		onCustomBtnEvent(opts){
			if (opts.btn.fn) {
				opts.btn.fn(opts);
			} else {
				this.$emit('onClickBtn', opts);
			}
		},


		/**
		 * 
		 * @param  {number} page 
		 */
		onChangeCurrentPage(page) {
			this.$emit('onChangeCurrentPage', page);
		},


		/**
		 * 
		 * @param  {number} page_size 
		 */
		onChangePageSize(page_size) {
			this.$emit('onChangePageSize', page_size);
		}
	},

	mounted() {
		// console.log(this.list);
	},

	/**
	 * 
	 * @type {Object}
	 */
	props: {
		List      : {
			type    : Array,
			required: true
		},
		FieldList : {
			type    : Array,
			required: true
		},
		BtnInfo   : {
			type: Object,
			default () {
				return {};
			}
		},
		Selection : {
			type   : Boolean,
			default: false
		},
		Expand    : {
			type: Object,
			default(){
				return {
					show    : false,
					position: "left"
				};
			}
		},
		Pagination: {
			type: Object,
			default () {
				return {};
			}
		},
		Search    : {
			type: Object,
			default(){
				return {};
			}
		}
	},


	/**
	 * 
	 * @type {Object}
	 */
	watch: {
		List(v) {
			if (v) {
				this.list = v;
			}
		},
		FieldList(v) {
			if (v) {
				this.fields = v;
			}
		},
		Selection(v) {
			this.selection = v;
		},
		Expand(v){
			this.expand = v;
		},
		BtnInfo(v) {
			this.btn_info = v;
		},
		Pagination(v) {
			this.pagination = v;
		},
		Search(v){
			this.search = v;
		}
	}
}


