export default {
    components: {
        // FormData
    },
    data() {
        return {
        	default_value:{
        		date_time:'',
				date_time_range:'',
			},
			fields: [{
				label: '',
				type: 'datetime',
				key: 'date_time',
				placeholder: '',
				events:{
					change:(value)=>{
						this.$message(''+value);
					}
				}
			}, {
				label: '',
				type: 'datetimerange',
				key: 'date_time_range',
				placeholder: '',
				events:{
					change:(value)=>{
						this.$message(''+value);
					}
				}
			}],

        }
    },
    methods: {
        onSubmit(data) {
            this.$emit('onTest', data);
        }
    }
}


