export default {
    components: {
        // FormData
    },
    data() {
        return {
            default_value:{
				editor:'<p>hello word</p>'
            },
            fields: [{
                label: '',
                key: 'editor',
                type: 'editor',
                desc: '',
                config:{
                    //tab
					// hide_menus:[
					// 	'head',
					// 	'bold',
					// ],

                    //tab
                    // show_menus:[
						// 'head',
						// 'bold',
                    // ],

                    //tabtab


                    //
                    attrs:{
						uploadImgServer:'/webapi/Article/editUpload',
						uploadFileName:'article',
						uploadImgHeaders:{
						    token:this.$store.state.user.userinfo.token
                        }
                    },


                    //
                    events:{
						customInsert:(insertImg, result, editor)=>{
                            if(result.status===404){
								this.$store.dispatch('remove_userinfo').then(() => {
									this.$alert(result.status + ',' + result.msg + '', '', {
										confirmButtonText: '',
										callback: action => {
											this.$router.push('/login');
										}
									});
								});
                            }else{
                                if(result.status!=200){
									this.$alert(result.status + ',' + result.msg + '', '', {
										confirmButtonText: '',
										callback: action => {
											this.$router.push('/login');
										}
									});
                                }else{
									insertImg(result.data.fileinfo.getSaveName);
                                }
                            }
						}
                    },


                    //change
                    onchange:(html)=> {

					}
                }
            }],
        }
    },
    methods: {
        onSubmit(data) {
            this.$emit('onTest', data);
        }
    }
}


