export default {
	name      : 'form-select',
	components: {

	},
	data() {
		return {
			default_value:{
				cate:'sanwen',
				tabs:['javascript','html'],
				custom_attr:'C'
			},
			fields: [{
				key  : 'cate',
				type : 'select',
				desc : '',
				label: '',
				list   : [{
					value: 'jishu',
					text : ''
				}, {
					value: 'sanwen',
					text : ''
				}, {
					value: 'qita',
					text : ''
				}],
				events:{
					change:({value,info})=>{
						this.$message(''+value+''+info);
					}
				}
			}, {
				key     : 'tabs',
				type    : 'select',
				desc    : '',
				label   : '',
				multiple: true,
				list   : [{
					value: 'html',
					text : 'HTML'
				}, {
					value: 'css',
					text : 'CSS'
				}, {
					value: 'javascript',
					text : 'JavaScript'
				}, {
					value: 'jquery',
					text : 'query'
				}],
				events:{
					change:({value,info})=>{
						this.$message(''+value.join(',')+''+info.join(','));
					}
				}
			},{
				key     : 'custom_attr',
				type    : 'select',
				desc    : '',
				label   : '',
				list   : [{
					v: 'H',
					t : 'HTML'
				}, {
					v: 'C',
					t : 'Css'
				}, {
					v: 'J',
					t : 'Javascript'
				}],
				custom_attrs:{
					label:'t',
					value:'v'
				},
				events:{
					change:({value,info})=>{
						this.$message(''+value+''+info);
					}
				}
			}],
		}
	},
	methods   : {
		onSubmit(data) {
			this.$emit('onTest', data);
		}
	},
	mounted(){

	}
}


