export default {
    name: 'custom-btn',
    components: {

    },
    data() {
        return {
            list: [],
            fields: [{
                key: 'name',
                label: ''
            }, {
                key: 'address',
                label: ''
            }],


            btn_info:{
                update_default :{
                    width:300,

                    // all:false,//false:

                    // default:false,//false:()

                    // select:false,//false:
					select_text:'',
					// update:false,//false:
					update_text:'',
					// delete:false,//false:
					delete_text:'',
					// add:false,//false
					add_text:'',

					// batch:false,//false:
					// batch_delete:false,//false:
					batch_delete_text:'',

                    //
                    list:[{
                        text:'',//
                        type:'warning',//elementUI
                        //onClickBtnonClickBtn
                        fn:(opts)=>{
                            this.$message('');
                            console.log(opts);
                        }
                    },{
						text:'',
                        type:'primary'
					}],
                    // list_position:'before'//after,before
                }
            },
        }
    },
	created(){
		for(let i=1;i<=3;i++){
			this.list.push({
				name:''+i,
				address:' '+i+" ",
			});
		}
	},
    methods: {
		onClickBtn(opts){
			this.$message('');
	        console.log(opts);
        },
		onClickBtnAdd(opts){
			this.$message('');
			console.log(opts);
		},
		onClickBtnSelect(opts){
			this.$message('');
			console.log(opts);
		},
		onClickBtnUpdate(opts){
			this.$message('');
		},
		onClickBtnDelete(opts){
			this.$message('');
		},
		onClickBtnBatchDelete(opts){
			this.$message('');
		},
		onSearch({data,info}){
			this.$message('');
		},
		onSelectionChange(opts){
			this.$message('');
		},
        init() {

        }
    },
    mounted() {
        this.init();
    },
    watch:{
        $route(){

        }
    }
}


