export default {
	name      : 'adv-article-list',
	components: {},
	data() {
		return {
			list  : [],
			fields: [{
				key  : 'id',
				label: 'ID'
			},{
				key  : 'create_time',
				label: ''
			}, {
				key  : 'title',
				label: ''
			}, {
				width          : '160',
				key            : 'status',
				label          : '',
				formatter      : function(item) {
					return item.status == 1 ? '' : '';
				},
				filter_list    : [{
					text : '',
					value: 1
				}, {
					text : '',
					value: 2

				}],
				filter_method  : function(value, item) {
					return item.status == value;
				},
				filter_multiple: false,
			}],

			//
			paginations: {
				current_page: 1,
				total       : 0,
				page_size   : 9,
				page_sizes  : [3, 9, 12, 24],
				layout      : "total, sizes, prev, pager, next, jumper"
			},

			//
			search_data: {
				fields       : [{
					key  : 'title',
					label: ''
				}],
				default_value: {
					title: ''
				}
			},

			//
			btn_info: {
				// batch:false,
				// batch_delete:false,
				width            : 300,
				add_text         : '',
				select_text      : '',
				update_text      : '',
				delete_text      : '',
				batch_delete_text: ''
			}

		}
	},
	methods   : {
		/**
		 * 
		 */
		onGetList(){
			this.$$api_article_selectArticle({
				data: {
					page     : this.paginations.current_page,
					page_size: this.paginations.page_size,
					title    : this.search_data.default_value.title
				},
				fn  : data => {
					console.log(data);
					this.paginations.total = data.list.total;
					this.list              = data.list.data;
				}
			});
		},


		/**
		 * 
		 */
		onClickBtnDelete(opts){
			this.$confirm('', '').then(() => {
				this.$$api_article_deleteArticle({
					data: {
						id: opts.data.id
					},
					fn  : data => {
						this.onGetList();
					}
				});
			});
		},


		/**
		 * 
		 */
		onClickBtnAdd(){
			this.$router.push('/adv/article/edit');
		},


		onClickBtnSelect(opts){
			console.log(opts);
			this.$message('');
		},


		/**
		 * 
		 * @param opts
		 */
		onClickBtnUpdate(opts){
			this.$router.push({
				path : '/adv/article/edit',
				query: {
					id: opts.data.id
				}
			});
		},


		/**
		 * 
		 * @param current_page    
		 */
		onChangeCurPage(current_page){
			var path           = this.$route.path,
				query          = Object.assign({}, this.$route.query);
			query.current_page = current_page;
			this.$router.push({
				path,
				query
			});
		},


		/**
		 * 
		 * @param page_size    
		 */
		onChangePageSize(page_size){
			var path        = this.$route.path,
				query       = Object.assign({}, this.$route.query);
			query.page_size = page_size;
			this.$router.push({
				path,
				query
			});
		},


		/**
		 * 
		 */
		onUpdateParams(){
			if (this.$route.query.current_page) {
				this.paginations.current_page = parseInt(this.$route.query.current_page);
			}
			if (this.$route.query.page_size) {
				this.paginations.page_size = parseInt(this.$route.query.page_size);
			}

			this.search_data.default_value.title = this.$route.query.title;
		},


		/**
		 * 
		 * @param data    
		 * @param info    
		 */
		onSearch({data, info}){
			console.log(data);
			console.log(info);

			var path  = this.$route.path,
				query = Object.assign({}, this.$route.query, data);

			this.$router.push({
				path,
				query
			});
		},


		//CheckBox
		onSelectionChange({ids,datas}){
			// console.log(ids);
			// console.log(datas);
		},

		/**
		 * 
		 * @param ids ids
		 * @param datas	
		 */
		onClickBtnBatchDelete({ids,datas}){
			this.$confirm(''+ids.join(','), '').then(() => {
				this.$$api_article_deleteArticle({
					data: {
						id: ids.join(',')
					},
					fn  : data => {
						this.onGetList();
					}
				});
			});
		},


		/**
		 * 
		 */
		init() {
			this.onUpdateParams();
			this.onGetList();
		}
	},
	mounted() {
		this.init();
	},
	watch     : {
		$route (to, from) {
			this.init();
		}
	}

}


